"use client";

import { motion } from "framer-motion";

export default function LeftSideLayout() {
  return (
    // ✅ Full-width left layout identical to GetStartedPage
    <div className="relative w-1/2 h-full overflow-hidden flex items-center justify-center bg-[#f7f7f7]">
      {/* Background image animation */}
      <motion.div
        initial={{ opacity: 0, scale: 1.05 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 1.2, ease: "easeOut" }}
        className="absolute top-0 left-0 h-full w-[90%] overflow-hidden z-0"
      >
        <img
          src="/images/getstarted-leftside2.png"
          alt="Background"
          className="w-full h-full object-cover"
        />
      </motion.div>

      {/* Foreground stacked images animation */}
      <div className="relative z-10 flex flex-col items-center justify-center space-y-12">
        {/* Guy Image */}
        <motion.img
          src="/images/guy.png"
          alt="Guy"
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.6, duration: 0.8, ease: "easeOut" }}
          className="object-contain w-[33%] h-auto -ml-32"
        />

        {/* Earn Money Image */}
        <motion.img
          src="/images/earn.png"
          alt="Earn"
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.9, duration: 0.8, ease: "easeOut" }}
          className="object-contain w-[70%] h-auto -ml-24"
        />
      </div>
    </div>
  );
}
